@keyframes appear{
    0%{
        transform: translate(0,200px);
        opacity: 0;
    }
    100%{
        transform: translate(0,0);
        opacity: 1;
    }
}

#icon-group{
    position: relative;
    width: 100%;
}

.icons{
    position: absolute;
    top: 40%;
    left: 40%;
    height: 100px;
    transition: 1s;
}

.active #icon-first{
    height: 400px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.active #icon-second{
    height: 150px;
    top: 50%;
    left: 50%;
    transform: translate(calc(-50% - 130px),calc(-50% + 70px));
    filter: blur(2px);
    opacity: 50%;
}

.active #icon-third{
    height: 200px;
    top: 50%;
    left: 50%;
    transform: translate(calc(-50% + 160px),calc(-50% + 20px));
    filter: blur(5px);
    opacity: 40%;
}

#back-text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    font-size: 150px;
    text-align: center;
    letter-spacing: 25px;
    font-weight: 700;
    opacity: 50%;
    color: #111111;
    filter: blur(3px);
}

.section:hover .marker{
    opacity: 1;
}

#intro{
    height: 300px;
    justify-content: center;
    background-color: #232323;
}

#display-box{
    overflow-y: hidden;
    width: 100%;
    margin-top: 40px;
    margin-bottom: 40px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.display-box-item{
    margin-left: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    background-color: #232323;
    width: 130px;
    height: 180px;
    display: flex;
    flex-shrink: 0;
}

.display-box-item .avatar{
    border-radius: 100%;
    margin-top: 25px;
    width: 70px;
    height: 70px;
}

.display-box-item .nickname{
    margin-top: 30px;
    text-align: center;
    color: #5c5c5c;
}

#future{
    align-items: center;
    background-color: #232323;
    height: 300px;
}

#statistic{
    background-color: #232323;
    height: 300px;
}

.stat-wrapper{
    width: 70%;
}

.stat-wrapper .stat-header{
    font-size: 35px;
    font-weight: 200;
    margin-top: 20px;
}

.stat-wrapper .stat-main{
    font-size: 30px;
    font-weight: 500;
}

#count{
    font-size: 100px;
    font-weight: 600;
}

.stat-wrapper .stat-footer{
    font-size: 18px;
    font-weight: 200;
    margin-right: 20px;
    margin-top: 40px;
    text-align: right;
}

.subtitle .text{
    width: 100%;
    text-align: center;
    font-size: 70px;
    font-weight: 600;
    text-shadow: #515151d6 15px -15px 3px;
}

#best_pos{
    font-size: 60px;
    font-weight: 800;
    margin-right: 20px;
}

#statistic{
    height: 350px;
}

.stat-title{
    font-size: 60px;
    letter-spacing: 3px;
    font-weight: 600;
    margin: 30px;
    margin-top: 80px;
}

.stat-subtitle{
    font-size: 40px;
    letter-spacing: 3px;
    margin: 30px;
    font-weight: 200;
    margin-top: 80px;
}

#chart{
    padding: 0;
}

#heatmap{
    margin:20px;
    height: 600px;
    width: calc(100% - 40px);
    border-radius: 10px;
}

#worst{
    background-color: #2c2c2c;
}

.canvas{
    height: 450px;
    width: 60%;
    margin: 30px;
    display: flex;
    justify-content: center;
}

#footer{
    height: 150px;
}